@import "../../style/abstracts/variables";
@import "../../style/abstracts/mixins";

.cart {
  &__wrapper {
    display: flex;
    flex-direction: column;
    min-height: 100%;
  }
  &__empty__wrapper {
    display: flex;
    justify-content: space-around;
    margin-top: 100px;
    height: 100%;
  }
  &__content__wrapper {
    margin-top: 100px;
    display: flex;
    @include responsive(phone){
      flex-direction: column;
      align-items: center;
    }
  }
  &__list {
    flex: 3;
    margin-left: 10px;
    margin-right: 40px;
    margin-top: 20px;
  }
  &__checkout-wrapper {
    flex: 2;
    min-height: 100vh;
    background-color: rgb(243, 243, 245);
    @include responsive(phone){
      width: 100%;
      margin: auto;
    }
  }
}
